<template>
    <div>
        <h1 class="centralizado">{{ titulo }}</h1>
        <h2 v-show="mensagem" class="centralizado">{{ mensagem }}</h2>
        <input type="search" class="filtro" @input="filtro = $event.target.value" placeholder="Filtre por parte do titulo">

        <ul class="lista-fotos">
            <li class="lista-fotos-item" v-for="foto of fotosComFiltro">
              <meu-painel :titulo="foto.titulo">
                <imagem-responsiva v-meu-transform:scale.animate="1.2" :url="foto.url" :titulo="foto.titulo"/>
                <router-link :to="{name: 'cadastro', params: {id: foto._id}}">
                  <meu-botao 
                    tipo="button"
                    rotulo="Alterar">
                  </meu-botao>
                </router-link>
                <meu-botao 
                  tipo="button" 
                  rotulo="Remover"
                  :confirmacao="false"
                  estilo="perigo" 
                  @botaoAtivado="remove(foto)">
                </meu-botao>
              </meu-painel>
            </li>
        </ul>
    </div>
</template>

<script>
import Painel from '../shared/painel/Painel.vue'
import ImagemResponsiva from '../shared/imagem/ImagemResponsiva.vue'
import Botao from '../shared/botao/Botao.vue';
import FotoService from '../../domain/foto/FotoService.js'

export default {
  components: {
    'meu-painel': Painel,
    'imagem-responsiva' : ImagemResponsiva,
    'meu-botao': Botao
  },

  methods: {
      remove(foto) {
          this.service.apaga(foto._id)
            .then(() => {
                let indice = this.fotos.indexOf(foto);
                this.fotos.splice(indice, 1);
                this.mensagem = 'Foto removida com sucesso!';
            }, 
                err => this.mensagem = err.message
            )
      }
  },

  computed:{
    fotosComFiltro(){
      if(this.filtro){
        let exp = new RegExp(this.filtro.trim(), 'i');
        return this.fotos.filter(foto =>  exp.test(foto.titulo));
      } else {
        return this.fotos;
      }
    }
  },

  data() {
    return {
      titulo: "Alurapic",
      fotos: [],
      filtro: '',
      mensagem: ''
    };
  },

  created() {

    this.service = new FotoService(this.$resource);
    this.service
      .lista()
      .then(fotos => (this.fotos = fotos), err => this.mensagem = err.message);
  }
};
</script>

<style>

.centralizado {
  text-align: center;
}

.lista-fotos {
  list-style: none;
}

.lista-fotos .lista-fotos-item {
  display: inline-block;
}

.filtro{
  display: block;
  width: 100%
}

</style>



